import React from "react";
import Logo from "../../assets/Logo.png";
import "./Nav.css";
import NavButton from "./NavButton/NavButton";
import MyAccountButton from "./MyAccountButton/MyAccountButton";
import { Link } from "react-router-dom";

export default function Nav(props) {
  return (
    <div className="nav">
      <img
        src={Logo}
        alt="Criss Fit Guide Logo"
        width="160px"
        className="logo"
      />
      <div className="navButtons">
        <Link to="/Criss-Fit-Guide/" style={{ color: "black" }}>
          <NavButton name="Home" isOn={props.isOnHome} />
        </Link>
        <Link to="/Criss-Fit-Guide/programs" style={{ color: "black" }}>
          <NavButton name="Programs" isOn={props.isOnPrograms} />
        </Link>
        <Link to="/Criss-Fit-Guide/about" style={{ color: "black" }}>
          <NavButton name="About Me" isOn={props.isOnAbout} />
        </Link>
      </div>
      <MyAccountButton />
    </div>
  );
}
