import React from "react";
import "./Programs.css";
import Nav from "../Nav/Nav";
import Footer from "../Footer/Footer";
import Splitter from "../../UI/Splitter/Splitter";
import Program1 from "../../assets/program1.png";
import Notes from "../ProgramsPage/Notes/Notes";

export default function Programs() {
  return (
    <>
      <Nav isOnPrograms="isOn" />
      <h1 className="title">MY FITNESS PROGRAMS</h1>
      <Splitter>
        <div className="img1">
          <img
            src={Program1}
            className="Programs1"
            width="400px"
            height="600px"
            alt="Criss Fit Guide Program"
          />
        </div>
        <Notes />
      </Splitter>
      <Footer />
    </>
  );
}
